.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    background-color: $backdropBg;

    opacity: 1;
    visibility: visible;
    overflow-y: scroll;

    transition: opacity 250ms $cubic, visibility 250ms $cubic;

    &--is-hidden {
        pointer-events: none;
        opacity: 0;
        visibility: hidden;
    }

}